import React from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import ListSubheader from "@mui/material/ListSubheader";

const dispatchedEvents = [];

const SrsMuiListSubheader = function (props, ref) {
  return <ListSubheader {...props} theme={props.muiTheme}>{props.content}</ListSubheader>;
};

const styles = `
  /* Custom styles for the MuiListSubheader component */
`;

const props = [
  {
    name: "content",
    defaultValue: "",
    type: "string",
  },
  {
    name: "color",
    defaultValue: "default",
    type: "string",
  },
  {
    name: "disableSticky",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object"
  }
];

const slots = [
  {
    name: "default",
    allowed: [],
  },
];

const WrappedComponent = wrapper(
  React.forwardRef(SrsMuiListSubheader),
  props,
  "mui"
);
export { WrappedComponent, styles, props, dispatchedEvents, slots };
